.MidOuter{
    /* border: 1px solid rgb(147, 26, 26); */
    width: 100%;
    height: 40px;
    background-color: #2bb673;
}
.MidContainer{
    /* border: 1px solid rgb(251, 142, 142); */
    width: 71%;
    height: 40px;
    display: flex;
    margin: auto;
    background-color: #2bb673;
    justify-content: space-between;
    /* padding: 0px 10px; */
}
.MidContainer >div{
    /* border: 1px solid rgb(103, 75, 226); */
    /* width: 12.5%; */
    background-color: #2bb673;
    height: 40px;
    margin-top: 0px;
    padding: 0px 5px;
  
}
.MidContainer button{
    border: none;
    /* margin-top: 5px; */
    height: 100%;
    padding: 0px 20px;
    font-weight: 400;
    font-size: 18px;
    color: white;
    background-color: #2bb673;
}
.MidContainer button:hover{
    cursor: pointer;
    color: rgb(235, 228, 228);
}